import { Injectable } from '@angular/core';
import { OStore, ObsValueReset, OStoreStart } from '@fireflysemantics/slice'

export const OSK: OStoreStart = {

  //=================================
  // CONFIGURATION
  //=================================

  APPLICATION_CONFIG: { value: null },

  //=================================
  // LOCAL STORAGE
  //=================================
  LOCAL_STORAGE_TERMS_OF_SERVICE_KEY: { value: null },
  IS_TERMS_OF_SERVICE_ACCEPTED: { value: false },

  //=================================
  // USER IDENTITY
  //=================================
  ON_SIGN_IN: { value: null },
  ON_SIGN_OUT: { value: null },
  AMPLIFY_USER: { value: null },
  USER: { value: null },
  USER_EMAIL: { value: null },
  IS_AUTHENTICATED: { value: false },

  //=================================
  // UI EVENTS
  //=================================
  ACTIVE_MENU: { value: null },

  //=================================
  // ROUTER EVENTS
  //=================================
  IS_APPLICATION_SHOWING: { value: false }
}

export interface OSKI {

  //=================================
  // CONFIGURATION
  //=================================
  APPLICATION_CONFIG: ObsValueReset

  //=================================
  // LOCAL STORAGE
  //=================================
  LOCAL_STORAGE_TERMS_OF_SERVICE_KEY: ObsValueReset
  IS_TERMS_OF_SERVICE_ACCEPTED: ObsValueReset

  //=================================
  // USER IDENTITY
  //=================================
  ON_SIGN_IN: ObsValueReset
  ON_SIGN_OUT: ObsValueReset
  AMPLIFY_USER: ObsValueReset
  USER: ObsValueReset
  USER_EMAIL: ObsValueReset
  IS_AUTHENTICATED: ObsValueReset


  //=================================
  // UI EVENTS
  //=================================
  ACTIVE_MENU: ObsValueReset,

  //=================================
  // ROUTER EVENTS
  //=================================
  IS_APPLICATION_SHOWING: ObsValueReset
}

@Injectable({
  providedIn: 'root'
})
export class StateService {
  os = new OStore<OSKI>(OSK)
  constructor() { }
}