import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ROUTES, MenuItems } from '../../../model';
import { StateService } from '../../../services/state/state.service';
import { AuthService } from '../../../services/auth/auth.service';
import { URLS } from '../../../model/'
import { BrowserService } from 'src/app/services';

@Component({
  selector: 'app-toolbar-menu',
  templateUrl: './toolbar-menu.component.html',
  styleUrls: ['./toolbar-menu.component.scss']
})
export class ToolbarMenuComponent implements OnInit {
  menuItems = MenuItems
  ROUTES = ROUTES
  URLS = URLS

  isAuthenticated() {
    return this.s.os.S.IS_AUTHENTICATED.obs
  }

  constructor(
    public b:BrowserService,
    public s: StateService,
    public r: Router,
    private auth: AuthService) { }
  onMenuClick(menu: string) {
    switch (menu) {
      case MenuItems.APPLICATION:
        this.r.navigate([ROUTES.APPLICATION])
        break;
      case MenuItems.WELCOME:
        this.r.navigate([ROUTES.WELCOME])
        break;
      case MenuItems.LOGIN:
        this.login()
        break;
      case MenuItems.LOGOUT:
        this.logout()
        break;
    }
    //IN case anything else needs to react 
    //to the currently active menu.
    this.s.os.put(this.s.os.S.ACTIVE_MENU, menu)
  }

  login() {
    const isTermOfServiceAccepted =
      this.s.os.snapshot(
        this.s.os.S.IS_TERMS_OF_SERVICE_ACCEPTED)

    isTermOfServiceAccepted
      ?
      this.auth.signInWithGoogle()
      :
      this.r.navigate([ROUTES.LOGIN])
  }

  logout() {
    this.auth.signOut()
  }

  ngOnInit(): void {
  }
}