import { Injectable } from '@angular/core'
import Auth, { CognitoHostedUIIdentityProvider } from '@aws-amplify/auth'
import { ICredentials, Hub } from '@aws-amplify/core'
import { StateService } from '../state/state.service'
import { ROUTES } from '../../model'
import { Router } from '@angular/router'
import { User, AMPLIFY_HUB_EVENTS } from '@fireflysemantics/amplify'

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  public static GOOGLE = CognitoHostedUIIdentityProvider.Google

  constructor(
    private s: StateService,
    private r: Router) {
    //    this.initializeAuthState()
    //    this.initializeUser()
    //    this.redirectToApplication()
  }

  signOut(): Promise<any> {
    return Auth.signOut()
      .then(() => this.s.os.post(this.s.os.S.IS_AUTHENTICATED, false))
  }

  socialSignIn(provider: CognitoHostedUIIdentityProvider): Promise<ICredentials> {
    return Auth.federatedSignIn({
      'provider': provider
    });
  }

  signInWithGoogle() {
    this.socialSignIn(AuthService.GOOGLE);
  }

  initializeAuthState() {
    Auth.currentAuthenticatedUser().
      then((u) => {
        this.s.os.put(this.s.os.S.IS_AUTHENTICATED, true)
      }).catch(() => {
        Hub.listen('auth', (data) => {
          const { channel, payload } = data
          if (channel === 'auth') {
            this.s.os.put(
              this.s.os.S.IS_AUTHENTICATED,
              payload.event == AMPLIFY_HUB_EVENTS.SIGN_OUT ? false : true)
          }
        })
      })
  }

  redirectToApplication() {
    this.s.os.S.IS_AUTHENTICATED.obs.subscribe(auth => {
      auth && this.r.navigate([ROUTES.APPLICATION])
      //        auth && this.r.navigate([{outlets: { application: 'application'}}])
    })
  }

  async initializeUser() {
    const user = await Auth.currentUserInfo()
    if (user) {
      const { email, name, given_name, family_name } = user.attributes
      const u: User = { email, name, given_name, family_name }
      this.s.os.put(this.s.os.S.USER, u)
    }
  }

  /**
   * We need to keep the user session
   * fresh so that credit card transactions
   * are guaranteed to connect.
   */
  async keepSessionFresh() {
    /*
    const cognitoUser = await Auth.currentAuthenticatedUser();
    const currentSession = await Auth.currentSession();
    await cognitoUser.refreshSession(
        currentSession.refreshToken, 
        (err, session) => {});*/
  }
}