import { Injectable } from '@angular/core';
import { StateService } from './state.service'
import { Observable, of } from 'rxjs';
import { ROUTES } from '../../model'
import { map, filter } from 'rxjs/operators'
import { Router, NavigationEnd } from '@angular/router'

@Injectable({
    providedIn: 'root'
})
export class RouterStateService {
    public applicationShowing$: Observable<boolean> =
        this.s.os.S.IS_APPLICATION_SHOWING.obs

    public applicationNotShowing$: Observable<boolean> =
        this.s.os.S.IS_APPLICATION_SHOWING.obs.pipe(map(v => !v))


    constructor(private s: StateService, private r: Router) {
        this.isApplicationShowing()
    }

    /**
     * The process button will
     * be enabled if the data map size is 1
     */
    isApplicationShowing() {
        const navEndEvents =
            this.r.events.pipe(
                filter(event => event instanceof NavigationEnd),
                map((event: NavigationEnd) => event.url))

        navEndEvents.subscribe(url => {

            switch (url) {
                case ROUTES.APPLICATION:
                    this.s.os.put(this.s.os.S.IS_APPLICATION_SHOWING, true)
                    break;
                default: {
                    this.s.os.put(this.s.os.S.IS_APPLICATION_SHOWING, false)
                }
            }
        })
    }
}