import { Injectable } from '@angular/core';
import { DomSanitizer } from "@angular/platform-browser";
import { MatIconRegistry } from '@angular/material/icon';
import { SVG } from '@fireflysemantics/constants'

@Injectable({
  providedIn: 'root'
})
export class AssetService {

  constructor(
    private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) {
    {
      this.matIconRegistry.addSvgIcon(
        SVG.FS_Q_LOGO.name,
        this.domSanitizer.bypassSecurityTrustResourceUrl(SVG.FS_Q_LOGO.url));

      this.matIconRegistry.addSvgIcon(
        SVG.FS_1_LOGO.name,
        this.domSanitizer.bypassSecurityTrustResourceUrl(SVG.FS_1_LOGO.url));

      this.matIconRegistry.addSvgIcon(
        SVG.FS_ALPHA_ALPHA.name,
        this.domSanitizer.bypassSecurityTrustResourceUrl(SVG.FS_ALPHA_ALPHA.url));

      this.matIconRegistry.addSvgIcon(
        SVG.GOOGLE_LOGO.name,
        this.domSanitizer.bypassSecurityTrustResourceUrl(SVG.GOOGLE_LOGO.url));

      this.matIconRegistry.addSvgIcon(
        SVG.FS_CSV_LOGO.name,
        this.domSanitizer.bypassSecurityTrustResourceUrl(SVG.FS_CSV_LOGO.url));
    }
  }
}