/**
 * When someone arrives to the application 
 * before they are allowed to proceed to the 
 * application they must be authenticated.
 */
import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Router
} from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators'
import { StateService } from '../state/state.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  constructor(
    private router: Router,
    private s: StateService) { }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    return this.s.os.S.IS_AUTHENTICATED.obs.pipe(map(authenticated => {
      if (!authenticated) {
        this.router.navigate([''])
        return false;
      }
      return true;
    }))
  }
}