import { Component, OnInit } from '@angular/core';
import { SVG } from '@fireflysemantics/constants'
import { FormControl } from '@angular/forms';
import { DataService } from 'src/app/services/data/data.service';
import { AuthService } from 'src/app/services/auth/auth.service';
import { StateService } from 'src/app/services/state/state.service';
import { untilDestroyed, UntilDestroy } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  SVG_CONSTANTS = SVG

  public tosControl: FormControl = new FormControl(false)

  constructor(
    private ds: DataService,
    private s: StateService,
    private auth: AuthService) {
    this.tosControl.
      valueChanges.pipe(untilDestroyed(this)).
      subscribe(
        (accepted: boolean) => {
          this.acceptTOS()
        })
  }

  /**Accept TOS And Track the Event with GA */
  acceptTOS() {
    this.ds.saveTermsOfServiceAccepted()
    this.s.os.put(
      this.s.os.S.IS_TERMS_OF_SERVICE_ACCEPTED,
      true)
  }

  isTOSAccepted() {
    return this.s.os.S.IS_TERMS_OF_SERVICE_ACCEPTED.obs
  }

  login() {
    this.auth.signInWithGoogle()
  }

  isAuthenticated() {
    return this.s.os.S.IS_AUTHENTICATED.obs
  }

  ngOnInit(): void { }
}